<template>
  <div class="singleDetail">
    <div class="public-detail">
      <div class="important-info-box">
        <div class="name">方案信息</div>
        <div class="btns">
          <Button @click="save('formValidate')">接受委托</Button>
          <Button type="primary" @click="save('formValidate')">拒绝委托</Button>
        </div>
      </div>
    </div>
    <div class="see-detail-box">
      <div class="transit-box">
        <div class="start-txt">重庆</div>
        <div class="route-box" v-for="(item,index) in 3">
          <div class="huoche">
            <adt-svg-icon iconClass="huoche"></adt-svg-icon>
          </div>
          <div class="changjiantou">
            <adt-svg-icon iconClass="changjiantou"></adt-svg-icon>
          </div>
          <div class="card-genre">国内集装箱</div>
          <div class="txt" :class="{'end-txt':index === 2}">香港</div>
        </div>
      </div>
      <Button @click="save('formValidate')">查看方案详情</Button>
    </div>
    <div class="tas-box">
      <span>单次物流委托：</span>
    </div>
    <div class="public-form-box">
      <div class="public-form-content">
        <Form
          ref="formValidate"
          label-position="right"
          :disabled="true"
          :label-width="152"
          :rules="formItemRule"
          :model="formModel"
        >
          <Row :gutter="20">
            <Col span="8">
              <p class="item-form-title">发货人信息</p>
              <FormItem label="发货人名称：" prop="transitModalF.attenName">
                <Input v-model="formModel.transitModalF.attenName"></Input>
              </FormItem>
              <FormItem label="代码：">
                <Input v-model="formModel.transitModalF.messageCode"></Input>
              </FormItem>
              <FormItem label="地址：" prop="transitModalF.attenAddr">
                <Input v-model="formModel.transitModalF.attenAddr"></Input>
              </FormItem>
              <FormItem label="电话：" prop="transitModalF.attenTel">
                <Input v-model="formModel.transitModalF.attenTel"></Input>
              </FormItem>
              <FormItem label="传真：">
                <Input v-model="formModel.transitModalF.attenFax"></Input>
              </FormItem>
              <FormItem label="邮箱：">
                <Input v-model="formModel.transitModalF.attenEmail"></Input>
              </FormItem>
            </Col>
            <Col span="8">
              <p class="item-form-title">收货人信息</p>
              <FormItem label="收货人名称：" prop="transitModalS.attenName">
                <Input v-model="formModel.transitModalS.attenName"></Input>
              </FormItem>
              <FormItem label="代码：">
                <Input v-model="formModel.transitModalS.messageCode"></Input>
              </FormItem>
              <FormItem label="地址：" prop="transitModalS.attenAddr">
                <Input v-model="formModel.transitModalS.attenAddr"></Input>
              </FormItem>
              <FormItem label="电话：" prop="transitModalS.attenTel">
                <Input v-model="formModel.transitModalS.attenTel"></Input>
              </FormItem>
              <FormItem label="传真：">
                <Input v-model="formModel.transitModalS.attenFax"></Input>
              </FormItem>
              <FormItem label="邮箱：">
                <Input v-model="formModel.transitModalS.attenEmail"></Input>
              </FormItem>
            </Col>
            <Col span="8">
              <p class="item-form-title">通知人信息</p>
              <FormItem label="通知人名称：" prop="transitModalT.attenName">
                <Input v-model="formModel.transitModalT.attenName"></Input>
              </FormItem>
              <FormItem label="地址：" prop="transitModalT.attenAddr">
                <Input v-model="formModel.transitModalT.attenAddr"></Input>
              </FormItem>
              <FormItem label="电话：" prop="transitModalT.attenTel">
                <Input v-model="formModel.transitModalT.attenTel"></Input>
              </FormItem>
              <FormItem label="传真：">
                <Input v-model="formModel.transitModalT.attenFax"></Input>
              </FormItem>
              <FormItem label="邮箱：">
                <Input v-model="formModel.transitModalT.attenEmail"></Input>
              </FormItem>
              <FormItem class="jz-meaningless">
                <template slot="label">无意义</template>
                <Input disabled></Input>
              </FormItem>
            </Col>
          </Row>
          <Row :gutter="20">
            <Col span="8">
              <p class="item-form-title">货物信息</p>
              <FormItem label="货物类型：" prop="entrustCargoReq.cargoType">
                <RadioGroup v-model="formModel.entrustCargoReq.cargoType">
                  <Radio label="普货">普货</Radio>
                  <Radio label="恒温货">恒温货</Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label="货物名称：" prop="entrustCargoReq.cargoName">
                <Input v-model="formModel.entrustCargoReq.cargoName"></Input>
              </FormItem>
              <FormItem label="唛头：" prop="entrustCargoReq.markNo">
                <Input v-model="formModel.entrustCargoReq.markNo"></Input>
              </FormItem>
              <FormItem label="HS编码：">
                <Input v-model="formModel.entrustCargoReq.hsCode"></Input>
              </FormItem>
              <FormItem label="毛重：" prop="entrustCargoReq.grossWeight" class="jz-input-group">
                <Input v-model="formModel.entrustCargoReq.grossWeight">
                  <Select
                    v-model="formModel.entrustCargoReq.cargoTotalUnit"
                    slot="append"
                    style="width: 70px"
                    placeholder
                  >
                    <Option value="kg">kg</Option>
                    <Option value="g">g</Option>
                  </Select>
                </Input>
              </FormItem>
              <FormItem label="净重：" prop="entrustCargoReq.netWeight" class="jz-input-group">
                <Input v-model="formModel.entrustCargoReq.netWeight">
                  <Select
                    v-model="formModel.entrustCargoReq.cargoTotalUnit"
                    slot="append"
                    style="width: 70px"
                    placeholder
                  >
                    <Option value="kg">kg</Option>
                    <Option value="g">g</Option>
                  </Select>
                </Input>
              </FormItem>
              <FormItem label="货物件数：" prop="entrustCargoReq.cargoNum">
                <Input v-model="formModel.entrustCargoReq.cargoNum"></Input>
              </FormItem>
              <FormItem label="货物体积：" prop="entrustCargoReq.cargoVol">
                <Input v-model="formModel.entrustCargoReq.cargoVol"></Input>
              </FormItem>
              <FormItem class="jz-meaningless">
                <template slot="label">无意义</template>
                <Input disabled></Input>
              </FormItem>
              <FormItem class="jz-meaningless">
                <template slot="label">无意义</template>
                <Input disabled></Input>
              </FormItem>
              <FormItem class="jz-meaningless">
                <template slot="label">无意义</template>
                <Input disabled></Input>
                <!-- <span style="height: 34px;display: inline-block;">huhamm</span> -->
              </FormItem>
            </Col>
            <Col span="8">
              <p class="item-form-title">一级包装</p>
              <FormItem label="包装类型：" prop="entrustPacka1.packType">
                <Input v-model="formModel.entrustPacka1.packType"></Input>
              </FormItem>
              <FormItem label="包装毛重：" prop="entrustPacka1.grossWeight">
                <Input v-model="formModel.entrustPacka1.grossWeight"></Input>
              </FormItem>
              <FormItem label="包装净重：" prop="entrustPacka1.netWeight">
                <Input v-model="formModel.entrustPacka1.netWeight"></Input>
              </FormItem>
              <FormItem label="包装件数：" prop="entrustPacka1.packNum">
                <Input v-model="formModel.entrustPacka1.packNum"></Input>
              </FormItem>
              <FormItem label="包装体积：" prop="entrustPacka1.packVol">
                <Input v-model="formModel.entrustPacka1.packVol"></Input>
              </FormItem>
              <p class="item-form-title">二级包装</p>
              <FormItem label="包装类型：">
                <Input v-model="formModel.entrustPacka2.packType"></Input>
              </FormItem>
              <FormItem label="包装毛重：">
                <Input v-model="formModel.entrustPacka2.grossWeight"></Input>
              </FormItem>
              <FormItem label="包装净重：">
                <Input v-model="formModel.entrustPacka2.netWeight"></Input>
              </FormItem>
              <FormItem label="包装件数：">
                <Input v-model="formModel.entrustPacka2.packNum"></Input>
              </FormItem>
              <FormItem label="包装体积：">
                <Input v-model="formModel.entrustPacka2.packVol"></Input>
              </FormItem>
            </Col>
            <Col span="8">
              <p class="item-form-title">运输要求</p>
              <FormItem label="主要运输方式：" prop="transportationAskReq.trspMode">
                <RadioGroup v-model="formModel.transportationAskReq.trspMode">
                  <Radio label="水运">水运</Radio>
                  <Radio label="铁运">铁运</Radio>
                  <Radio label="空运">空运</Radio>
                  <Radio label="公路">公路</Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label="物流类型：" prop="transportationAskReq.logisticsType">
                <RadioGroup v-model="formModel.transportationAskReq.logisticsType">
                  <Radio label="国内">国内</Radio>
                  <Radio label="国际">国际</Radio>
                  <Radio label="综保区">综保区</Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label="提箱类型：" prop="transportationAskReq.suitcaseType">
                <RadioGroup v-model="formModel.transportationAskReq.suitcaseType">
                  <Radio label="场装">场装</Radio>
                  <Radio label="提装">提装</Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label="集装箱数量：">
                <Input v-model="formModel.transportationAskReq.suitcaseUnm"></Input>
              </FormItem>
              <FormItem label="货物适宜温度：">
                <Input v-model="formModel.transportationAskReq.setTemperatureAt">
                  <Select
                    v-model="formModel.transportationAskReq.setTemperatureUnit"
                    slot="append"
                    style="width: 70px"
                    placeholder
                  >
                    <Option value="°C">°C</Option>
                    <Option value="k">k</Option>
                  </Select>
                </Input>
              </FormItem>
              <FormItem label="出口报关地：">
                <Input v-model="formModel.transportationAskReq.exPort"></Input>
              </FormItem>
              <FormItem label="进口报关地：">
                <Input v-model="formModel.transportationAskReq.imPort"></Input>
              </FormItem>
              <FormItem label="最迟启运时间：">
                <DatePicker
                  type="datetime"
                  placeholder="Select date"
                  v-model="formModel.transportationAskReq.lastStartTime"
                ></DatePicker>
              </FormItem>
              <FormItem label="最低时长要求：">
                <Input v-model.number="formModel.transportationAskReq.lowestTimeReq">
                  <Select
                    v-model="formModel.transportationAskReq.lowestTimeUnit"
                    slot="append"
                    style="width: 70px"
                    placeholder
                  >
                    <Option value="天">天</Option>
                    <Option value="小时">小时</Option>
                  </Select>
                </Input>
              </FormItem>
              <FormItem label="最高费用要求：">
                <Input v-model="formModel.transportationAskReq.highestCostReq"></Input>
              </FormItem>
              <FormItem label="预计货好时间：">
                <DatePicker
                  type="datetime"
                  placeholder="Select date"
                  v-model="formModel.transportationAskReq.cargoReadyTime"
                ></DatePicker>
              </FormItem>
            </Col>
          </Row>
          <Row :gutter="20">
            <Col span="8">
              <p class="item-form-title">服务参数</p>
              <FormItem label="运输类型：" prop="transportationAskReq.trspType">
                <RadioGroup v-model="formModel.transportationAskReq.trspType">
                  <Radio label="集装箱">集装箱</Radio>
                  <Radio label="件杂货">件杂货</Radio>
                </RadioGroup>
              </FormItem>
            </Col>
            <Col span="8">
              <p class="item-form-title"></p>
              <FormItem label="集装箱来源：">
                <RadioGroup v-model="formModel.transportationAskReq.containerSource">
                  <Radio label="自备">自备</Radio>
                  <Radio label="租箱">租箱</Radio>
                </RadioGroup>
              </FormItem>
            </Col>
            <Col span="8">
              <p class="item-form-title"></p>
              <FormItem label="集装箱尺寸：">
                <RadioGroup v-model="formModel.transportationAskReq.contaSize">
                  <Radio label="20">20</Radio>
                  <Radio label="40">40</Radio>
                  <Radio label="60">60</Radio>
                </RadioGroup>
              </FormItem>
            </Col>
          </Row>
        </Form>
      </div>
    </div>
  </div>
</template>

<script>
import { apiOnceEntrustInfoAdd } from "@/api/logisticsCommission";
import validateRules from "@/libs/validateRules";
import moment from "moment";
const initForm = {
  transitModalF: {},
  transitModalS: {},
  transitModalT: {},
  // 表单  货物信息
  entrustCargoReq: {
    cargoTotalUnit: "kg"
  },
  // 表单  包装信息
  entrustPacka1: {},
  entrustPacka2: {},
  // 表单  运输要求
  transportationAskReq: {
    lowestTimeUnit: "天",
    setTemperatureUnit: "°C"
  }
};
export default {
  name: "singleDetail",
  data() {
    return {
      animal: "单次",
      law: "规律性周期委托",
      // 表单
      formModel: {},
      formItemRule: {
        "transitModalF.attenName": [validateRules.required("名称")[0]],
        "transitModalF.attenAddr": [validateRules.required("地址")[0]],
        "transitModalF.attenTel": [
          validateRules.required("电话")[0],
          validateRules.phone()[0]
        ],
        "transitModalS.attenName": [validateRules.required("名称")[0]],
        "transitModalS.attenAddr": [validateRules.required("地址")[0]],
        "transitModalS.attenTel": [
          validateRules.required("电话")[0],
          validateRules.phone()[0]
        ],
        "transitModalT.attenName": [validateRules.required("名称")[0]],
        "transitModalT.attenAddr": [validateRules.required("地址")[0]],
        "transitModalT.attenTel": [
          validateRules.required("电话")[0],
          validateRules.phone()[0]
        ],
        // cargoType: [validateRules.required("货物类型")[0]],
        "entrustCargoReq.cargoType": [
          { required: true, message: "货物类型", trigger: "change" }
        ],
        "entrustCargoReq.cargoName": [validateRules.required("货物名称")[0]],
        "entrustCargoReq.markNo": [validateRules.required("唛头")[0]],
        "entrustCargoReq.grossWeight": [validateRules.required("毛重")[0]],
        "entrustCargoReq.netWeight": [validateRules.required("净重")[0]],
        "entrustCargoReq.cargoNum": [validateRules.required("货物件数")[0]],
        "entrustCargoReq.cargoVol": [validateRules.required("货物体积")[0]],
        "entrustPacka1.packType": [validateRules.required("包装类型")[0]],
        "entrustPacka1.grossWeight": [validateRules.required("包装毛重")[0]],
        "entrustPacka1.netWeight": [validateRules.required("包装净重")[0]],
        "entrustPacka1.packNum": [validateRules.required("包装件数")[0]],
        "entrustPacka1.packVol": [validateRules.required("包装体积")[0]],
        "transportationAskReq.trspMode": [
          validateRules.required("主要运输方式")[0]
        ],
        "transportationAskReq.logisticsType": [
          validateRules.required("物流类型")[0]
        ],
        "transportationAskReq.suitcaseType": [
          validateRules.required("提箱类型")[0]
        ],
        "transportationAskReq.trspType": [validateRules.required("运输类型")[0]]
        // accountStatus: [validateRules.select("账户状态")[0]]
      }
    };
  },
  created() {
    this._initData();
  },
  methods: {
    _initData() {
      this.formModel = initForm;
    },
    save(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this._add();
          this.$Message.success("Success!");
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    _add() {
      // jiekou
      let cargoReadyTime = moment(
        this.formModel.transportationAskReq.cargoReadyTime
      ).valueOf();
      let lastStartTime = moment(
        this.formModel.transportationAskReq.lastStartTime
      ).valueOf();
      let data = {
        attenReqList: [
          {
            ...this.formModel.transitModalF,
            attenType: "发货人"
          },
          {
            ...this.formModel.transitModalS,
            attenType: "收货人"
          },
          {
            ...this.formModel.transitModalT,
            attenType: "通知人"
          }
        ],
        entrustPackagingReqList: [
          {
            ...this.formModel.entrustPacka1,
            packPcode: "0"
          },
          {
            ...this.formModel.entrustPacka2
          }
        ],
        entrustCargoReq: {
          ...this.formModel.entrustCargoReq
        },
        transportationAskReq: {
          ...this.formModel.transportationAskReq,
          cargoReadyTime,
          lastStartTime
        },
        logisticsSchemeCode: "bcdhbchdbhbd",
        freightReqCode: "bcdhbchdbhbd",
        contractCode: "bcdhbchdbh",
        departurePort: "bcdhbchdbhbd",
        destinationPort: "bcdhbchdbhbd",
        trsspmPort: ["hhh", "hhh", "shbsh"]
      };
      apiOnceEntrustInfoAdd({ data }).then(({ data }) => {
        console.log(data);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.singleDetail {
  .important-info-box {
    display: flex;
    justify-content: space-between;
    /deep/.ivu-btn{
      margin-left: 20px;
    }
  }
  .public-form-box {
    width: 100%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 4px 0px rgba(51, 51, 51, 0.1);
    padding: 20px;
    .public-form-content {
      background: rgba(249, 249, 249, 1);
      padding: 0 20px 20px 20px;
      /deep/.ivu-row {
        margin-bottom: 16px;
      }
      .item-form-title {
        height: 35px;
        font-size: 16px;
        font-weight: bold;
        color: rgba(51, 51, 51, 1);
        line-height: 35px;
      }
      .ivu-form-item {
        margin-bottom: 0;
        /deep/.ivu-form-item-label {
          background: #f5f6f8;
          border: 1px solid #e6e6e6;
          border-bottom: none;
          vertical-align: middle;
        }
        /deep/.ivu-form-item-content {
          padding-left: 4px;
          padding-right: 4px;
          border: 1px solid #e6e6e6;
          border-bottom: none;
          border-left: none;
          background: #fff;
          vertical-align: middle;
        }
        /deep/.ivu-select,
        /deep/.ivu-input {
          width: 146px;
          background: rgba(249, 249, 249, 1);
          border-radius: 4px;
          .ivu-select-selection {
            background: rgba(249, 249, 249, 1);
          }
        }
        /deep/.ivu-input-group {
          width: 60%;
        }
        /deep/.ivu-form-item-error-tip {
          top: 6px;
          left: 152px;
        }
        // 给最后一行添加
        &:last-child {
          /deep/.ivu-form-item-label {
            border-bottom: 1px solid #e6e6e6;
          }
          /deep/.ivu-form-item-content {
            border-bottom: 1px solid #e6e6e6;
          }
        }
      }
      // 额外加的包裹层
      .jz-meaningless {
        /deep/.ivu-form-item-label {
          color: #f5f6f8 !important;
        }
      }
      .jz-input-group {
        /deep/.ivu-form-item-error-tip {
          left: 225px !important;
        }
      }
    }
  }

  .tas-box {
    padding: 4px 40px;
    /deep/.ivu-radio-wrapper {
      &:last-child {
        margin-left: 94px;
      }
    }
  }
  .see-detail-box {
    padding: 0px 40px;
    height: 100px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 4px 0px rgba(51, 51, 51, 0.1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }
  .transit-box {
    height: 100%;
    display: flex;
    align-items: center;
    .start-txt,
    .end-txt {
      height: 24px;
      font-size: 24px;
      color: rgba(51, 51, 51, 1);
      line-height: 28px;
    }
    .route-box {
      padding: 0 30px;
      position: relative;
      .changjiantou {
        color: #02adb5;
        height: 30px;
        margin: 0 auto;
      }
      .huoche {
        color: #ffbb32;
        height: 20px;
        margin: 0 auto;
        position: absolute;
        top: -5px;
      }
      .card-genre {
        position: absolute;
        top: 14px;
        color: $light-color;
        top: 22px;
        left: 146px;
      }
      .txt {
        position: absolute;
        text-align: center;
        color: #333333;
        left: 326px;
        width: 64px;
        top: 7px;
      }
      .end-txt {
        color: #999;
      }
    }
  }
}
// 不要的
.public-form {
  .transit12 {
    .ivu-form-item {
      /deep/.ivu-form-item-label {
        width: 100px !important;
        padding-right: 14px;
      }
      /deep/.ivu-form-item-content {
        padding-left: 6px;
        padding-right: 10px;
        width: 50%;
      }
      /deep/.ivu-input {
        width: 100px;
      }
    }
  }
}
</style>